<template>
    <div class="container">
      <main class="content">
        <h1>Consumo de Consumibles</h1>
        <p class="gasto-total"><strong>Gasto mensual en consumibles:</strong> {{ calcularGastoTotal() }} €</p>
        
        <div class="data-card">
          <h2>Estimaciones de Consumo</h2>
          <p><strong>Consumo Anual (Consumibles de Oficina):</strong> {{ calcularConsumoAnual(consumiblesOficina) }} €</p>
          <p><strong>Consumo Anual (Productos de Limpieza):</strong> {{ calcularConsumoAnual(consumiblesLimpieza) }} €</p>
          
          <input v-model.number="diasEstimados" type="number" placeholder="Introduce días" min="1" :max="365" />
          <button @click="calcularConsumoPeriodo">Calcular Consumo en Período</button>
          
          <p v-if="consumoEstimadoOficina !== null">
            <strong>Consumo en {{ diasEstimados }} días (Oficina):</strong> {{ consumoEstimadoOficina }} €
          </p>
          <p v-if="consumoEstimadoLimpieza !== null">
            <strong>Consumo en {{ diasEstimados }} días (Limpieza):</strong> {{ consumoEstimadoLimpieza }} €
          </p>
        </div>
  
        <div class="pizarra-section">
          <h2>Material de Pizarra</h2>
          <div class="grid-container">
            <div class="data-card small" v-for="(item, index) in consumiblesPizarra" :key="index">
              <h3>{{ item.nom }}</h3>
              <p><strong>Cantidad Mensual:</strong> {{ item.quantitat }} unidades</p>
              <p><strong>Precio Unitario:</strong> {{ item.preu_unitat }} €</p>
              <p><strong>Gasto Mensual:</strong> {{ (item.quantitat * item.preu_unitat).toFixed(2) }} €</p>
            </div>
          </div>
          <button class="pizarra-btn" @click="usarPizarraElectronica">Usar Pizarra Electrónica</button>
        </div>
        
        <div class="data-card" v-for="(item, index) in consumiblesNoPizarra" :key="index">
          <h3>{{ item.nom }}</h3>
          <p><strong>Cantidad Mensual:</strong> {{ item.quantitat }} unidades</p>
          <p><strong>Precio Unitario:</strong> {{ item.preu_unitat }} €</p>
          <p><strong>Gasto Mensual:</strong> {{ (item.quantitat * item.preu_unitat).toFixed(2) }} €</p>
          
          <button v-if="esPapel(item.nom)" @click="reducirConsumoPapel(index)">
            Reducir Consumo de Papel
          </button>
        </div>
  
        <button class="controls" @click="restablecerValores">Restablecer Valores</button>
  
      </main>
    </div>
  </template>
  
  <script>
  import consumiblesData from "@/data/consumibles.json";
  
  export default {
    data() {
      return {
        consumibles: JSON.parse(JSON.stringify(consumiblesData.dades_mensuals)),
        diasEstimados: "",
        consumoEstimadoOficina: null,
        consumoEstimadoLimpieza: null
      };
    },
    computed: {
      consumiblesPizarra() {
        return this.consumibles.filter(item => this.esMaterialPizarra(item.nom));
      },
      consumiblesNoPizarra() {
        return this.consumibles.filter(item => !this.esMaterialPizarra(item.nom));
      },
      consumiblesOficina() {
        return this.consumibles.filter(item => !this.esProductoLimpieza(item.nom));
      },
      consumiblesLimpieza() {
        return this.consumibles.filter(item => this.esProductoLimpieza(item.nom));
      }
    },
    methods: {
      calcularGastoTotal() {
        return this.consumibles.reduce((total, item) => total + (item.quantitat * item.preu_unitat), 0).toFixed(2);
      },
      calcularConsumoAnual(lista) {
        return (lista.reduce((total, item) => total + (item.quantitat * item.preu_unitat), 0) * 12).toFixed(2);
      },
      calcularConsumoPeriodo() {
        if (!this.diasEstimados || this.diasEstimados < 1 || this.diasEstimados > 365) {
          alert("Introduce un número de días válido (1 - 365).");
          return;
        }
        this.consumoEstimadoOficina = ((this.calcularConsumoAnual(this.consumiblesOficina) / 365) * this.diasEstimados).toFixed(2);
        this.consumoEstimadoLimpieza = ((this.calcularConsumoAnual(this.consumiblesLimpieza) / 365) * this.diasEstimados).toFixed(2);
      },
      usarPizarraElectronica() {
        this.consumibles.forEach(item => {
          if (this.esMaterialPizarra(item.nom)) {
            item.quantitat = 0;
          }
        });
      },
      reducirConsumoPapel(index) {
        if (this.consumibles[index].quantitat > 0) {
          this.consumibles[index].quantitat -= 1;
        }
      },
      esMaterialPizarra(nombre) {
        return nombre.toLowerCase().includes("marcador") || nombre.toLowerCase().includes("borrador");
      },
      esPapel(nombre) {
        return nombre.toLowerCase().includes("papel") && !nombre.toLowerCase().includes("wc") && !nombre.toLowerCase().includes("secamanos");
      },
      esProductoLimpieza(nombre) {
        return nombre.toLowerCase().includes("jabon") || nombre.toLowerCase().includes("papel wc") || nombre.toLowerCase().includes("secamanos") || nombre.toLowerCase().includes("bolsas basura");
      },
      restablecerValores() {
        this.consumibles = JSON.parse(JSON.stringify(consumiblesData.dades_mensuals));
        this.diasEstimados = "";
        this.consumoEstimadoOficina = null;
        this.consumoEstimadoLimpieza = null;
      }
    }
  };
  </script>
  
